<template>
    <nav id="navbar" class="navbar fixed-top">
        <div id="navbar" class="container-header py-2">
            <div class="d-flex flex-row align-items-center">
                <div class="col-4">
                    <a href="/">
                        <img class="img-fluid" alt="logo tukarjual.com" src="@/assets/site-logo.webp">
                    </a>
                </div>
                <div class="col nopadding">
                    <ul class="nav float-right">
                        <li class="nav-item">
                            <span @click="$router.push({name:'search',query:{from:$route.name}})" class="nav-link">
                                <i class="fas fa-search"></i>
                            </span>
                        </li>
                        <li class="nav-item">
                            <a v-if="$store.state.cart.cart_product==null&&$store.state.login.ifLogin" href="#" class="nav-link">
                                <i class="fa fa-spinner fa-spin"></i>
                            </a>
                            <router-link class="nav-link" :to="{name:'cart',query:{from:$route.name}}">
                                <i class="fas fa-shopping-cart"></i>
                                <span class="badge count_cart">{{$store.getters.cart_product_length}}</span>
                            </router-link>
                        </li>
                        <li v-if="!$store.state.login.ifLogin" class="nav-item">
                            <!-- <router-link :to="{name:'register',query:{from:$route.name}}" class="nav-link"> -->
                            <router-link :to="{name:'register'}" class="nav-link">
                                <!-- <i class="fas fa-sign-in-alt"></i> -->
                                <span class="btn btn-sm tjc-border btn-outline-warning rounded-pill">Daftar</span>
                            </router-link>
                        </li>
                        <li v-else class="nav-item dropdown">
                            <a class="nav-link dropdown-toggle" href="#" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                <i class="fas fa-user"></i>
                            </a>
                            <div class="dropdown-menu dropdown-menu-right">
                                <router-link class="dropdown-item" :to="{name:'profile', query:{from:$route.name}}"><i class="fas fa-user"></i> Profile</router-link>
                                <router-link class="dropdown-item" :to="{name:'history', query:{from:$route.name}}"><i class="fas fa-exchange-alt"></i> Transaksi</router-link>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    </nav>
</template>
<script>
    export default {
        name: "DefaultMenu",
        mounted(){
            window.addEventListener("scroll", this.handleScroll);
        },
        destroyed () {
            window.removeEventListener('scroll', this.handleScroll);
        },
        methods:{
            handleScroll(){
                if (document.body.scrollTop > 80 || document.documentElement.scrollTop > 80) {
                    document.querySelector("#navbar").style.backgroundColor="white";
                    // document.querySelector(".nav").style.color="#017E99";
                    document.querySelector(".nav-link").style.color="#017E99";
                } else {
                    document.querySelector("#navbar").style.backgroundColor="transparent";
                    // document.querySelector(".nav").style.color="#F39811";
                    document.querySelector(".nav-link").style.color="#F39811";
                }
            }
        }
    }
</script>