<template>
    <div class="row">
		<div class="col-12 p-0">
			<!-- <div class="page-header pt-5 mt-2"> -->
			<div class="page-header">
				<div id="carouselControls" class="carousel slide" data-ride="carousel">
					<div class="carousel-inner">
						<div class="carousel-item" :class="promo.length==0 ? 'active' : ''" v-if="promo.length==0">
							<img class="d-block w-100" src="@/assets/empty-banner.svg">
						</div>
						<div v-else v-for="(data, key) in promo" :key="key" class="carousel-item" :class="key==0 ? 'active' : ''">
							<img class="lazy d-block w-100" src="@/assets/empty-banner.svg" :data-src="$store.state.url_banner+data.gambar" alt="Promo TukarJual">
						</div>
					</div>
					<a class="carousel-control-prev" href="#carouselControls" role="button" data-slide="prev">
						<span class="carousel-control-prev-icon" aria-hidden="true"></span>
						<span class="sr-only">Previous</span>
					</a>
					<a class="carousel-control-next" href="#carouselControls" role="button" data-slide="next">
						<span class="carousel-control-next-icon" aria-hidden="true"></span>
						<span class="sr-only">Next</span>
					</a>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
export default {
	name:"Carousel",
	data(){
		return {
			promo : []
		}
	},
	async created(){
		await this.get_promo();
	},
	async updated(){
		await this.$parent.lazyLoad();
	},
	methods:{
		get_promo: function () {
			this.axios.get(this.$store.state.url_api + "promotions/banners").then(response => {
				this.promo = response.data;
			}).catch(error => {
				console.log(error);
				this.promo = [];
			})
		},
	}
}
</script>
<style lang="css" scoped>
	.row{
		margin-right:0;
		margin-left:0;
	}
</style>