<template>
  <div class="konten">
    <DefaultMenu />
      <keep-alive>
        <Carousel />
      </keep-alive>
      <keep-alive>
        <DefaultCategory  />
      </keep-alive>
      <!-- <keep-alive>
        <Footer />
      </keep-alive> -->
      <ModalPDAM :ids="'pdam'" :message="{head: 'PDAM Kotabaru', title: 'Login untuk cek tagihan PDAM', deskripsi: 'Nikmati kemudahan cek tagihan PDAM Kotabaru', gambar:'PDAM-Kotabaru.png'}" />
      <ModalPDAM :ids="'trio-motor'" :message="{head: 'Trio Motor Kotabaru', title: 'Login untuk cek layanan Trio Motor', deskripsi: 'Nikmati layanan perbaikan dan kebutuhan sparepart kendaraan Pian', gambar:'trio-motor.png'}" />
      <PdamMessage />
      <router-view :key="$route.fullPath"></router-view>
  </div>
</template>

<script>
import DefaultMenu from '@/components/Menu/DefaultMenu.vue'
import Carousel from '@/components/Carousel/Carousel.vue'
import DefaultCategory from '@/components/Category/DefaultCategory.vue'
import ModalPDAM from '@/components/modal/Pdam.vue'
import PdamMessage from '@/components/modal/PdamMessage.vue'
// import Footer from '@/components/Footer/Footer'
export default {
  name: 'Home',
  components: {
    DefaultMenu,
    Carousel,
    DefaultCategory,
    // Footer,
    ModalPDAM,
    PdamMessage
  },
  methods:{
    lazyLoad: function () {
			var lazyloadImages;

			if ("IntersectionObserver" in window) {
        lazyloadImages = document.querySelectorAll(".lazy");
				var imageObserver = new IntersectionObserver(entries => {
					entries.forEach(entry => {
						if (entry.isIntersecting) {
							var image = entry.target;
							image.src = image.dataset.src;
							image.classList.remove("lazy");
							imageObserver.unobserve(image);
						}
					});
				});
				lazyloadImages.forEach(image => {
					imageObserver.observe(image);
				});
			}
		},
  }
}
</script>